.bloques_cola{
    display: flex;
    height: 900px;
    width: 100%;
 
    flex-direction: row-reverse;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    overflow: auto;
}

.container__bloquesCola{
    
    background: rgb(238,174,202);
    background: linear-gradient(165deg, rgba(238,174,202,1) 0%, rgba(148,187,233,1) 100%);
    height: 100px;
    width: 100px;
    
    border: 1px solid;
    padding: auto;
    text-align:center;border:1px dotted #000; padding:8px;
}

